.modal {
  background: rgba(247, 243, 232, 0.82);
}
.modal .modal-content {
  border: none;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
}
.modal .close-modal {
  position: absolute;
  top: 0;
  right: 0;
}
.modal .close-modal:focus {
  box-shadow: none;
}
.modal .modal-title {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 50px;
}

.order-form .form-footer {
  margin-top: 2rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.order-form .files-wrap {
  display: flex;
}
.order-form .file-block {
  margin-right: 40px;
}
.order-form .submit {
  padding: 15px 75px;
  font-size: 14px;
  text-transform: uppercase;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-button {
  padding: 0;
  border: none;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-button:focus {
  box-shadow: none;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-button i {
  color: #3B7A17;
  margin-left: 5px;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .meta {
  margin-right: 0;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .meta .upload-remove-button {
  top: 0;
  right: 6px;
  line-height: 1;
  padding: 0;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .info h4 {
  max-width: 200px;
}
.order-form .responsiv-uploader-fileupload.style-file-multi .upload-object .info p.size {
  display: none !important;
}

@media (max-width: 1199px) {
  .order-form .form-control {
    max-width: 220px;
  }
  .order-form .file-block {
    margin-right: 20px;
    width: 50%;
  }
  .order-form .submit {
    padding: 15px 45px;
  }
  .order-form .responsiv-uploader-fileupload.style-file-multi .upload-button {
    text-align: left;
    font-size: 14px;
  }
}
@media (max-width: 991px) {
  .order-form .form-footer,
.order-form .files-wrap {
    flex-direction: column;
    align-items: flex-start;
  }
  .order-form .form-footer {
    margin-top: 30px;
  }
  .order-form .submit {
    width: 100%;
  }
  .order-form .file-block {
    margin-right: 0;
    margin-bottom: 20px;
    width: 100%;
  }
  .order-form .file-btn {
    text-align: left;
  }
  .order-form .form-control {
    max-width: 100%;
    flex: 1 1;
  }
  .order-form .form-label {
    min-width: 190px;
  }
}
.order-modal .modal-content {
  padding: 80px 40px 60px;
}

@media (max-width: 1199px) {
  .order-modal .modal-content {
    padding: 40px 20px;
  }
}
.success-modal {
  font-size: 24px;
  text-align: center;
}
.success-modal .modal-dialog {
  max-width: 715px;
}
.success-modal .success-wrap {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 552px;
}
.success-modal .success-message-logo {
  width: 170px;
  margin-bottom: 40px;
}
.success-modal .success-message-title {
  text-transform: uppercase;
  margin-bottom: 30px;
}
.success-modal .success-message-order {
  margin-bottom: 40px;
}

@media (max-width: 767px) {
  .success-modal {
    font-size: 18px;
  }
  .success-modal .modal-content {
    min-height: 350px;
  }
}
.hero {
  background-color: #fff;
  background-image: url(../../assets/images/svg/hero-bg.svg);
  background-position: center;
  padding: 50px 0;
}
.hero .h1 {
  margin: 40px 0;
  line-height: 1.5;
  font-weight: 600;
}
.hero ul li {
  position: relative;
  padding-left: 15px;
  font-size: 24px;
  font-weight: 500;
  color: #7B4A2A;
}
.hero ul li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 17px;
  width: 4px;
  height: 4px;
  background-color: #7B4A2A;
  border-radius: 50%;
}

.calculate {
  background-color: #fcfcfc;
  padding-bottom: 60px;
}

@media (max-width: 1199px) {
  .hero ul li {
    font-size: 18px;
  }
  .hero ul li::before {
    top: 12px;
  }

  .calculate {
    padding-bottom: 30px;
  }
}
@media (max-width: 991px) {
  .hero .hero-box {
    display: none;
  }
}